import React, { Fragment, Component } from 'react';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import ReactSlider from 'react-slider'

import style from './onBoarding.module.css'

import Switch from "react-switch";
import getTranslation from "../../util/translation";

import qrCode from '../../assets/img/onBoarding/qr_onboarding.jpg'
import api from "../../util/api";

class OnBoarding extends Component {


    constructor(props) {
        super(props);
        let locale = localStorage.getItem('locale');
        if(!locale) {
           locale = localStorage.setItem('locale','ru')
        }
        this.state = {
            locale,
            premiumVisible: false,
            route_id: null,
            tourName: '',
            name: '',
            age: '',
            preferences: [],
            preferencesOptions: ['Reading', 'Hiking', 'Fishing', 'Crafts', 'Movies', 'Collecting', 'Music', 'Gardening','Video Games', 'Animals', 'Sport', 'Shopping'],
            goal: 1,
            hunger: 100,
            adventure: 100,
            play: 100,
    }

        this.slider = React.createRef()
    }

    toggleLang = () => {
        this.setState({
            locale: this.state.locale === 'en' ? 'ru' : 'en'
        }, () => {
            localStorage.setItem('locale', this.state.locale)

        })
    }

    onLogin = () => {

        let data = {
            code: 'login',
            name: this.state.name,
            age: parseInt(this.state.age, 10),
            preference: this.state.preferences,
            target: 2,
            type_emotion: [this.state.hunger, this.state.adventure, this.state.play]
        }

        const location = new URL(window.location)

        if (location.searchParams.get('route_id')) {
            data['route_id'] = parseInt(location.searchParams.get('route_id'), 10)
        } else {
            data['route_id'] = 2
        }

        api.post('login', {...data}).then((res) => {

            this.setState({
                pending: false
            })

            if (res.data.accessToken) {
                this.props.setToken(res.data.accessToken)
            }
        }).catch(() => {
            this.setState({
                pending: false
            })

        })
    }

    render() {

        const { locale, premiumVisible, preferences, preferencesOptions } = this.state

        const settings = {
            dots: false,
            infinite: false,
            speed: 500,
            slidesToShow: 1,
            slidesToScroll: 1,
            arrows: false
        }

        if (premiumVisible) {
            return (
                <div className={style.premiumVisible}>
                    <div className={style.premiumVisibleContent}>
                        <div className={style.premiumBody}>
                            <div className={style.premiumBodyItem}>
                                <div className={style.premiumBodyItemTitle}>
                                    {getTranslation('premiumTitle1', locale)}
                                </div>
                                <div className={style.premiumBodyItemText}>
                                    {getTranslation('premiumText1', locale)}
                                </div>
                            </div>
                            <div className={style.premiumBodyItem}>
                                <div className={style.premiumBodyItemTitle}>
                                    {getTranslation('premiumTitle2', locale)}
                                </div>
                                <div className={style.premiumBodyItemText}>
                                    {getTranslation('premiumText2', locale)}
                                </div>
                            </div>
                            <div className={style.premiumBodyItem}>
                                <div className={style.premiumBodyItemTitle}>
                                    {getTranslation('premiumTitle3', locale)}
                                </div>
                                <div className={style.premiumBodyItemText}>
                                    {getTranslation('premiumText3', locale)}
                                </div>
                            </div>
                            <div className={style.premiumBodyItem}>
                                <div className={style.premiumBodyItemTitle}>
                                    {getTranslation('premiumTitle4', locale)}
                                </div>
                                <div className={style.premiumBodyItemText}>
                                    {getTranslation('premiumText4', locale)}
                                </div>
                            </div>
                        </div>
                        <div className={style.premiumBodyItemTitle}>
                            {getTranslation('tryTitle', locale)}
                        </div>
                        <div className={style.premiumBodyItemText}>
                            {getTranslation('tryText', locale)}
                        </div>
                        <div className={style.buyButton}>
                            <button>
                                {getTranslation('tryButton', locale)}
                            </button>
                        </div>
                        <div className={style.cancelPremiumView}>
                            <button onClick={() => {
                                this.setState({
                                    premiumVisible: false
                                })
                            }}>
                                {getTranslation('tryLater', locale)}
                            </button>
                        </div>
                    </div>
                </div>
            )
        }

        return (

            <Fragment>
                <div style={{
                    position: 'absolute',
                    right: '20px',
                    top: '20px',
                    zIndex: '2'
                }}>
                    <Switch
                        offColor={'#08f'}
                        onColor={'#CF142B'}
                        onChange={this.toggleLang}
                        checked={locale === 'en'}
                        uncheckedIcon={<div style={{
                            color: '#e1e1e1',
                            fontWeight: 400,
                            fontSize: '14px',
                            padding: '5px'
                        }}>
                            ru
                        </div>}
                        checkedIcon={<div style={{
                            color: '#e1e1e1',
                            fontWeight: 400,
                            fontSize: '14px',
                            padding: '5px'
                        }}>
                            en
                        </div>}
                    />
                </div>

                <Slider {...settings} ref={this.slider}>
                    <div className={style.slide}>
                        <div className={style.premiumWrap}>
                            <button onClick={() => {
                                this.setState({
                                    premiumVisible: true
                                })
                            }}>
                                {getTranslation('onBoarding_premium', locale)}
                            </button>
                        </div>
                        <div className={style.image}>
                            <img src={qrCode} alt=""/>
                        </div>
                        <div className={style.body}>
                            <div className={style.inputWrap}>
                                <input type="text" className={style.input}
                                       value={this.state.tourName}
                                       onChange={(e) => {
                                           this.setState({
                                               tourName: e.target.value
                                           })
                                       }}
                                       placeholder={'Tour name'}/>
                            </div>
                            <div className={style.dots}>
                                {[0,1,2,3].map((dot, dotIndex) => {
                                    return (
                                        <button
                                            key={dotIndex}
                                            className={`${style.dot} ${0 === dotIndex ? `${style.dotActive}` : ''}`}
                                                onClick={() => {
                                                    this.slider.current.slickGoTo(0)
                                                }}
                                        />
                                    )
                                })}
                            </div>
                            <div className={style.nextWrap}>
                                <button onClick={() => {
                                    this.slider.current.slickGoTo(1)
                                }}>
                                    {getTranslation('next', locale)}
                                </button>
                            </div>
                            <div className={style.skipWrap}>
                                <button onClick={this.props.onConfirm}>
                                    {getTranslation('skip', locale)}
                                </button>
                            </div>
                        </div>

                    </div>
                    <div className={style.slide}>
                        <div className={style.body}>

                            <div className={style.body}>
                                <div className={style.inputWrap}>
                                    <input type="text" className={style.input}
                                           value={this.state.name}
                                           onChange={(e) => {
                                               this.setState({
                                                   name: e.target.value
                                               })
                                           }}
                                           placeholder={'name'}/>
                                </div>
                                <div className={style.inputWrap}>
                                    <input type="text" className={style.input}
                                           value={this.state.age}
                                           onChange={(e) => {
                                               this.setState({
                                                   age: e.target.value
                                               })
                                           }}
                                           placeholder={'age'}/>
                                </div>
                                <div style={{
                                    padding: '20px',
                                    textAlign: 'center'
                                }}>
                                    or
                                </div>
                                <div className={style.loginInstBtn}>
                                    <button onClick={this.props.onConfirm}>
                                        {getTranslation('signInInst', locale)}
                                    </button>
                                </div>
                            </div>

                            <div className={style.dots}>
                                {[0,1,2,3].map((dot, dotIndex) => {
                                    return (
                                        <button
                                            key={dotIndex}
                                            className={`${style.dot} ${1 === dotIndex ? `${style.dotActive}` : ''}`}
                                                onClick={() => {
                                                    this.slider.current.slickGoTo(1)
                                                }}
                                        />
                                    )
                                })}
                            </div>
                            <div className={style.nextWrap}>
                                <button onClick={() => {
                                    this.slider.current.slickGoTo(2)
                                }}>
                                    {getTranslation('next', locale)}
                                </button>
                            </div>
                            <div className={style.skipWrap}>
                                <button onClick={this.props.onConfirm}>
                                    {getTranslation('skip', locale)}
                                </button>
                            </div>
                        </div>
                    </div>
                    <div className={style.slide}>
                        <div className={style.body}>
                            <div className={style.selectedInterests}>
                                {getTranslation('selectedInterests', locale)}
                                <div style={{
                                    marginLeft: '20px'
                                }}>
                                    {preferences.length}
                                </div>

                            </div>
                            <div className={style.interestsGrid}>
                                {preferencesOptions.map((item) => {

                                    const isActive = preferences.includes(item)

                                    return (
                                        <div key={item}
                                             onClick={() => {

                                                 let newPreferenses = [...preferences]
                                                 if (isActive) {
                                                     newPreferenses = newPreferenses.filter((pref) => pref !== item)
                                                 } else {
                                                     newPreferenses = [...newPreferenses, item]
                                                 }

                                                 this.setState({
                                                     preferences: newPreferenses
                                                 })
                                             }}
                                             className={`${style.interestsGridItem} ${isActive ? style.interestsGridItemActive : ''}`}>
                                            <div className={style.interestsGridItemText}>
                                                {item}
                                            </div>
                                        </div>
                                    )
                                })}
                            </div>


                            <div className={style.dots}>
                                {[0,1,2,3].map((dot, dotIndex) => {
                                    return (
                                        <button key={dotIndex}
                                            className={`${style.dot} ${2 === dotIndex ? `${style.dotActive}` : ''}`}
                                                onClick={() => {
                                                    this.slider.current.slickGoTo(2)
                                                }}
                                        />
                                    )
                                })}
                            </div>
                            <div className={style.nextWrap}>
                                <button onClick={() => {
                                    this.slider.current.slickGoTo(3)
                                }}>
                                    {getTranslation('next', locale)}
                                </button>
                            </div>
                            <div className={style.skipWrap}>
                                <button onClick={this.props.onConfirm}>
                                    {getTranslation('skip', locale)}
                                </button>
                            </div>
                        </div>
                    </div>
                    <div className={style.slide}>
                        <div className={style.body} style={{
                                marginTop: '40px'
                            }}>

                            <div className={style.sliderWrap}>
                                <div className={style.inputLabel}>
                                    Hunger
                                </div>
                                <ReactSlider
                                    defaultValue={100}
                                    className="horizontal-slider"
                                    thumbClassName="example-thumb"
                                    trackClassName="example-track"
                                    onChange={val => {
                                        this.setState({
                                            hunger: val
                                        })
                                    }}
                                    renderThumb={(props, state) => <div {...props}>{state.valueNow}</div>}
                                />
                            </div>
                            <div className={style.sliderWrap}>
                                <div className={style.inputLabel}>
                                    Adventure
                                </div>
                                <ReactSlider
                                    defaultValue={100}
                                    className="horizontal-slider"
                                    thumbClassName="example-thumb"
                                    trackClassName="example-track"
                                    onChange={val => {
                                        this.setState({
                                            adventure: val
                                        })
                                    }}
                                    renderThumb={(props, state) => <div {...props}>{state.valueNow}</div>}
                                />
                            </div>
                            <div className={style.sliderWrap}>
                                <div className={style.inputLabel}>
                                    Play
                                </div>
                                <ReactSlider
                                    defaultValue={100}
                                    className="horizontal-slider"
                                    thumbClassName="example-thumb"
                                    trackClassName="example-track"
                                    onChange={val => {
                                        this.setState({
                                            play: val
                                        })
                                    }}
                                    renderThumb={(props, state) => <div {...props}>{state.valueNow}</div>}
                                />
                            </div>

                            <div className={style.dots}>
                                {[0,1,2,3].map((dot, dotIndex) => {
                                    return (
                                        <button key={dotIndex}
                                            className={`${style.dot} ${3 === dotIndex ? `${style.dotActive}` : ''}`}
                                                onClick={() => {
                                                    this.slider.current.slickGoTo(3)
                                                }}
                                        />
                                    )
                                })}
                            </div>
                            <div className={style.nextWrap}>
                                <button onClick={() => {
                                    this.onLogin()
                                }}>
                                    {getTranslation('getStarted', locale)}
                                </button>
                            </div>
                        </div>
                    </div>
                </Slider>
            </Fragment>
        )

    }


}

export default OnBoarding